body.theme-spring-flowers::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 200%;
  background-image: radial-gradient(circle at 15% 10%, rgba(236, 72, 153, 0.5) 0%, transparent 4%),
    radial-gradient(circle at 45% 8%, rgba(219, 39, 119, 0.4) 0%, transparent 3.5%),
    radial-gradient(circle at 75% 15%, rgba(236, 72, 153, 0.45) 0%, transparent 3.8%),
    radial-gradient(circle at 85% 20%, rgba(219, 39, 119, 0.4) 0%, transparent 3.5%),
    radial-gradient(circle at 25% 40%, rgba(236, 72, 153, 0.4) 0%, transparent 3%),
    radial-gradient(circle at 55% 45%, rgba(219, 39, 119, 0.35) 0%, transparent 3.2%),
    radial-gradient(circle at 80% 50%, rgba(236, 72, 153, 0.4) 0%, transparent 3%),
    radial-gradient(circle at 20% 70%, rgba(219, 39, 119, 0.45) 0%, transparent 3.5%),
    radial-gradient(circle at 60% 75%, rgba(236, 72, 153, 0.4) 0%, transparent 3.2%),
    radial-gradient(circle at 90% 85%, rgba(219, 39, 119, 0.35) 0%, transparent 3%);
  animation: petals-fall 20s linear infinite;
  pointer-events: none;
  z-index: 1;
}
body.theme-spring-flowers::after {
  content: '';
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 180px;
  background: linear-gradient(180deg, 
    rgba(34, 197, 94, 0.3) 0%,
    rgba(22, 163, 74, 0.6) 50%,
    rgba(21, 128, 61, 0.8) 100%
  );
  box-shadow: 0 -20px 40px rgba(34, 197, 94, 0.4);
  transform-origin: bottom center;
  animation: grass-sway 6s ease-in-out infinite;
  pointer-events: none;
  z-index: 1;
}
@keyframes petals-fall {
  0% {
    transform: translateY(-100px) rotate(0deg);
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  100% {
    transform: translateY(600px) rotate(360deg);
    opacity: 0;
  }
}
@keyframes grass-sway {
  0%, 100% {
    transform: skewX(0deg);
  }
  25% {
    transform: skewX(0.8deg);
  }
  75% {
    transform: skewX(-0.8deg);
  }
}
@keyframes leaves-drift {
  0% {
    transform: translateY(-100px) translateX(0) rotate(0deg);
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  25% {
    transform: translateY(150px) translateX(20px) rotate(15deg);
  }
  50% {
    transform: translateY(300px) translateX(-20px) rotate(-15deg);
  }
  75% {
    transform: translateY(450px) translateX(15px) rotate(10deg);
  }
  100% {
    transform: translateY(600px) translateX(-10px) rotate(-10deg);
    opacity: 0;
  }
}
@keyframes leaves-drift-2 {
  0% {
    transform: translateY(-150px) translateX(-15px) rotate(-5deg);
    opacity: 0;
  }
  12% {
    opacity: 1;
  }
  30% {
    transform: translateY(180px) translateX(-30px) rotate(-20deg);
  }
  55% {
    transform: translateY(330px) translateX(25px) rotate(18deg);
  }
  80% {
    transform: translateY(480px) translateX(-18px) rotate(-12deg);
  }
  100% {
    transform: translateY(650px) translateX(12px) rotate(8deg);
    opacity: 0;
  }
}
@keyframes leaves-drift-3 {
  0% {
    transform: translateY(-80px) translateX(10px) rotate(8deg);
    opacity: 0;
  }
  8% {
    opacity: 1;
  }
  28% {
    transform: translateY(160px) translateX(-25px) rotate(-18deg);
  }
  52% {
    transform: translateY(320px) translateX(28px) rotate(20deg);
  }
  78% {
    transform: translateY(470px) translateX(-22px) rotate(-14deg);
  }
  100% {
    transform: translateY(620px) translateX(15px) rotate(12deg);
    opacity: 0;
  }
}
@keyframes bloom {
  0%, 100% {
    transform: rotate(-45deg) scale(1);
    box-shadow: 0 10px 30px rgba(236, 72, 153, 0.5),
      inset -5px -5px 15px rgba(0, 0, 0, 0.2),
      inset 5px 5px 15px rgba(255, 255, 255, 0.3);
  }
  50% {
    transform: rotate(-45deg) scale(1.1);
    box-shadow: 0 15px 40px rgba(236, 72, 153, 0.7),
      inset -5px -5px 15px rgba(0, 0, 0, 0.3),
      inset 5px 5px 20px rgba(255, 255, 255, 0.4);
  }
}
